import React from 'react'
import Avatar from '@mui/material/Avatar';
import TypeIt from "typeit-react";

export default function IntroSection() {
  return (
    <>
        <div style= {{
            marginTop: 20,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        }}>
        <Avatar
        alt="Sanchez Barry"
        src="/photo.png"
        sx={{ width: 200, height: 200 }}
        />
        </div>

        <div style= {{
            fontFamily: 'Helvetica',
            marginTop: 20,
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        }}>
        <TypeIt
        options={{
            speed: 40
        }}
        getBeforeInit={(instance) => {
            instance.type("Hi, I'm Sanchez. I'm a Software Engineer.").pause(750).delete(18).pause(500).type("Full Stack Developer.")
            .pause(750).delete(21).pause(500).type("Front-End Tinkerer.")
            .pause(750).delete(19).pause(500).type("Back-End Builder.");

            // Remember to return it!
            return instance;
        }}
        />;
        </div>
</>
  )
}
